*{
    box-sizing: border-box;
}
/* Text Editor */
.textEditor{
    font-family: "Custom Font";
    height:578px;
    width:440px;
    border: 1px solid blue;
    overflow-x: hidden;
    outline: none;
    padding: 15px;
    overflow-y: scroll;
}

.page{
    background-image: linear-gradient(#999 0.05em, transparent 0.1em);
    background-size: 100% 30px;
    height:568px;
    margin: auto;
    width:400px;
    display: inline-block;
    /* overflow: hidden; */
    /* word-break: break-all; */
    outline: none;
    border: 1px solid black;
    /* background: repeating-linear-gradient( white, white 28px, grey 29px, grey 30px); */
    margin-bottom: 40px;
}
.page{
    line-height: 30px;   
} 
.page:focus{
    border: 1px solid rgb(61, 197, 61);
}

/* Text Editor */


/* Image List */
.imageList{
    overflow-x: scroll;
    display: flex;
    flex-direction: row;
    max-width: 100%;
    height: 600px;
}
